* {
  margin: 0;
}

@import url(https://fonts.googleapis.com/css2?family=Russo+One&amp;display=swap);

h1 {
  text-align: center;
  font-size: 0.6em;
  margin-bottom: 0px;
  margin-top: 5px;
  font-style: italic;
  font-family: Russo One;
}

body {
  font-size: 24px;
  background-color: #00b4fc;
}

h2 {
  border-radius: 4px;
  margin-top: 3px;
  margin-bottom: 3px;
  font-size: 0.75em;
  padding-top: 3px;
  font-weight: bold;
  box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.4);
}

#drum-machine {
  margin-top: 10px;
  height: 100vh;
  display: grid;
  align-items: center;
  justify-content: center;
}

.wrapper {
  width: auto;
  height: auto;
  display: flex;
  padding: 10px;
  border: 5px solid orange;
  box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.4);
  grid-gap: 10px;
  margin-top: 30px;
  border-radius: 10px;
  grid-template-columns: repeat(3, 1fr);
  background-image: linear-gradient(-235deg, #005bc5 45%, #b7dbfa 80%);
}

.keyboard {
  display: grid;
  gap: 5px;
  cursor: pointer;
  grid-template-columns: repeat(3, 1fr);
}

.keyboard .drum-pad {
  font-size: 1.5vw;
  cursor: pointer;
  width: 6.5vw;
  height: auto;
  border-radius: 7px;
  background-color: #17f9ff;
  box-shadow: black 3px 3px 5px;
  transition: all 0.3s ease-in-out;
}

.keyboard .drum-pad:hover {
  background-color: #ffbc11;
}

.controle {
  width: 30vh;
  height: auto;
  text-align: center;
  padding-left: 15px;
  margin-top: -5px;
  margin-bottom: 0px;
  display: grid;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.controle > button,
.controle > input,
.controle > h2 {
  margin-bottom: 5px;
}

.controle button {
  justify-content: center;
  align-items: center;
  border: none;
  color: #ffffff;
  margin-top: 5px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  padding: 4px 7px;
  border-radius: 5px;
  background-color: #000000;
  cursor: pointer;
}

.controle button:hover {
  background-color: white;
  color: black;
}

@media screen and (max-width: 650px) {
  .wrapper {
    display: flex;
    flex-direction: column;
  }

  .controle {
    margin: auto;
    padding-left: 5px;
  }

  .keyboard .drum-pad {
    width: auto;
    font-size: 3vw;
  }

  .keyboard {
    height: 25vh;
  }
}
